import React from 'react';
import PropTypes from 'prop-types';
import { bounceInLeft } from 'react-animations';
import styled, { keyframes } from 'styled-components';
import { Link } from 'react-scroll';

const Wrapper = styled.p`
  width: 70%;
  line-height: 1.6;
  animation: ${props => (props.active ? keyframes`${bounceInLeft}` : '')} 1s;
  @media (max-width: 876px) {
    width: 100%;
    order: 1;
  }
`;

const SkillsetLink = styled.span`
  text-decoration: underline;
  color: dodgerblue;
  cursor: pointer;
`;

const Paragraph = props => (
  <Wrapper active={props.active}>
    Hello, welcome to my portfolio, my name is Ahmed Tarek and I am a 17 year
    old Front End Web Developer from Alexandria, Egypt. I have been coding for
    about 2 years now and I am already going in back end development but I'm
    still a noob. I specialize in React/Redux applications since that's the
    framework I like the most and I have other experiences with other
    frameworks, libraries, module bundlers...etc. You can check my{' '}
    <Link to="skillset" spy={true} smooth={true}>
      <SkillsetLink>full skills set here</SkillsetLink>
    </Link>{' '}
    and be sure to check out my{' '}
    <a
      href="https://github.com/ahmedtarek2134"
      target="_blank"
      rel="noopener noreferrer"
      style={{ color: 'dodgerblue' }}
    >
      github
    </a>{' '}
    and{' '}
    <a
      href="https://codepen.io/ahmedtarek2134"
      target="_blank"
      rel="noopener noreferrer"
      style={{ color: 'dodgerblue' }}
    >
      codepen
    </a>{' '}
    for my projects &lt;3
  </Wrapper>
);

Paragraph.propTypes = {
  active: PropTypes.bool.isRequired,
};

export default Paragraph;



